.hiui-btn {
  display: inline-block;
  margin-bottom: 0;
  font-weight: 500;
  text-align: center;
  cursor: pointer;
  border: 1px solid transparent;
  white-space: nowrap;
  height: 34px;
  line-height: 1.5;
  padding: 0 20px;
  font-size: 16px;
  border-radius: 4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  position: relative;
  color: rgba(0,0,0,0.65);
  background-color: #fff;
  border-color: #d9d9d9;
  outline: none;
}
.hiui-btn.disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
.hiui-btn-sm {
  padding: 0 12px;
  font-size: 14px;
  height: 24px;
}
.hiui-btn-md {
  padding: 0 20px;
  font-size: 16px;
  height: 34px;
}
.hiui-btn-lg {
  padding: 0 30px;
  font-size: 18px;
  height: 44px;
}


